.wp-block-site-functionality-faq {
  border-radius: 0.325rem;
  white-space: normal;
  background-color: #20A78C;
  box-shadow: 0.125rem 0.125rem 0.5rem rgba(0, 0, 0, 0.7);
  transition-property: background-color;
  transition-duration: 0.25s;
  transition-timing-function: cubic-bezier();
  padding: 2rem;
  padding-bottom: 0;
  margin-bottom: 1rem;
  color: #fff;
  cursor: pointer;
}
@media (min-width: 1080px) {
  .wp-block-site-functionality-faq {
    padding: 1.5rem 2.375rem;
    padding-bottom: 0;
    max-width: 1080px;
  }
}
.wp-block-site-functionality-faq .question {
  position: relative;
}
.wp-block-site-functionality-faq .question h3 {
  max-width: 87%;
  font-size: 1.618rem;
  margin-top: 2rem;
}
@media (min-width: 720px) {
  .wp-block-site-functionality-faq .question h3 {
    font-size: 1.618rem;
  }
}
.wp-block-site-functionality-faq .question h3 a {
  text-decoration: none;
}
.wp-block-site-functionality-faq .question .chevron {
  position: absolute;
  width: 3rem;
  right: -2rem;
  top: 0;
  transition-property: transform;
  transition-duration: 0.5s;
  transition-timing-function: cubic-bezier();
  transform: translateX(-25%);
}
@media (min-width: 1080px) {
  .wp-block-site-functionality-faq .question .chevron {
    right: 0;
  }
}
.wp-block-site-functionality-faq .answer {
  padding-top: 1rem;
  max-height: 0;
  height: 0;
  overflow: hidden;
  transition-property: max-height, height;
  transition-duration: 0.5s;
  transition-timing-function: ease-out;
  padding-bottom: 3.375rem;
}
@media (min-width: 1080px) {
  .wp-block-site-functionality-faq .answer {
    max-width: 54rem;
  }
}
.wp-block-site-functionality-faq .answer .answer-wrapper {
  position: relative;
  top: 3rem;
  opacity: 0;
  transition-property: opacity, top;
  transition-duration: 0.5s;
  transition-delay: 0.125s;
  transition-timing-function: ease-out;
}
.wp-block-site-functionality-faq .answer a {
  color: #fff;
  text-decoration: underline;
}
.wp-block-site-functionality-faq.active {
  background-color: #146656;
}
.wp-block-site-functionality-faq.active .chevron {
  transform: translateX(-25%) rotate(90deg);
}
.wp-block-site-functionality-faq.active .answer {
  transition-property: max-height, height;
  transition-duration: 0.5s;
  transition-timing-function: ease-out;
  max-height: 300vh;
  height: 100%;
}
.wp-block-site-functionality-faq.active .answer .answer-wrapper {
  opacity: 1;
  top: 0;
}

